import React from "react";

import "./MarkdownTopic.css";

import Container from "@material-ui/core/Container";
import ReactMarkdown from "react-markdown";
import CodeBlock from "./CodeBlock";

class MarkdownTopic extends React.Component {
  constructor(props) {
    super(props);

    this.getPostFolder = this.getPostFolder.bind(this);
    this.loadPost = this.loadPost.bind(this);

    this.state = {
      md: "",
      folder: ""
    };
  }

  getPostFolder(thisPost) {
    if (typeof thisPost === "undefined" || thisPost === null) return null;
    const { folder } = thisPost;
    return folder;
  }

  loadPost(folder) {
    if (folder === null) return;
    fetch(`/md/${folder}/post.md`)
      .then((res) => res.text())
      .then((res) => {
        this.setState({ md: res });
        if (typeof this.props.didLoad === "function") this.props.didLoad();
      });
  }

  componentDidMount() {
    const folder = this.getPostFolder(this.props.topic);
    if (this.state.folder !== folder) {
      this.setState({ folder }, () => {
        this.loadPost(folder);
      });
    }
  }

  componentDidUpdate() {
    const folder = this.getPostFolder(this.props.topic);
    if (this.state.folder !== folder) {
      this.setState({ folder }, () => {
        this.loadPost(folder);
      });
    }
  }

  render() {
    return (
      <>
        <Container maxWidth="md">
          <ReactMarkdown
            source={this.state.md}
            renderers={{ code: CodeBlock }}
            transformImageUri={(a, b, c) => {
              return `/md/${this.getPostFolder(this.props.topic)}/${a}`;
            }}
          />
        </Container>
      </>
    );
  }
}

export default MarkdownTopic;
