import React from "react";

import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";

import LogoComponent from "./LogoComponent";

import { Link } from "react-router-dom";

import "./MainPage.css";

class MainPage extends React.Component {
  constructor(props) {
    super(props);

    this.handleAboutClick = this.handleAboutClick.bind(this);

    this.state = {
      svg: null
    };
  }

  componentDidMount() {
    document.getElementsByClassName("appBody")[0].classList.add("landingPage");
  }

  componentWillUnmount() {
    document
      .getElementsByClassName("landingPage")[0]
      .classList.remove("landingPage");
  }

  handleAboutClick() {
    this.props.onAboutClick();
  }

  render() {
    console.log({ main: this.props });
    return (
      <div className="container">
        <div className="logo">
          <LogoComponent />
        </div>
        <div className="buttons">
          <Button
            variant="contained"
            size="large"
            color="primary"
            component={Link}
            to="/about"
          >
            About
          </Button>
          <Button
            variant="contained"
            size="large"
            color="primary"
            component={Link}
            to="/blog"
          >
            Blog
          </Button>
        </div>
        <div className="social-buttons">
          <IconButton
            onClick={() => {
              window.open("https://github.com/robranders");
            }}
          >
            <GitHubIcon fontSize="large" />
          </IconButton>
          <IconButton
            onClick={() => {
              window.open(
                "https://www.linkedin.com/in/robin-branders-b1838a188"
              );
            }}
          >
            <LinkedInIcon fontSize="large" />
          </IconButton>
        </div>
      </div>
    );
  }
}

export default MainPage;
