import React from "react";

import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";

import MarkdownTopic from "./MarkdownTopic";
import ToolbarComponent from "../../components/ToolbarComponent";

import { Link, Redirect } from "react-router-dom";

class MarkdownTopics extends React.Component {
  constructor(props) {
    super(props);

    this.postDidLoad = this.postDidLoad.bind(this);

    this.state = {
      loading: true
    };
  }

  componentDidMount() {
    document.getElementsByClassName("appBody")[0].classList.add("aboutPage");
  }

  componentWillUnmount() {
    document
      .getElementsByClassName("aboutPage")[0]
      .classList.remove("aboutPage");
  }

  postDidLoad() {
    this.setState({ loading: false });
  }

  render() {
    console.log({ md: this.props });
    const topic = parseInt(this.props.match.params.topic);
    if (isNaN(topic)) return <Redirect push to="/blog" />;
    const topicPost = this.props.posts[topic];
    if (
      Array.isArray(this.props.posts) &&
      this.props.posts.length > 0 &&
      typeof topicPost === "undefined"
    )
      return <Redirect push to="/blog" />;
    // if (
    //   (typeof topicPost === "undefined" || topicPost === null) &&
    //   typeof this.props.posts === "undefined"
    // )
    //   return <Redirect push to="/blog" />;
    return (
      <>
        <ToolbarComponent />
        <Container maxWidth="md">
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="center"
            spacing={2}
            style={{ marginTop: "10px" }}
          >
            <Grid item>
              <Button
                variant="contained"
                startIcon={<ArrowBackIosIcon />}
                disableElevation
                disabled={!(topic > 0) || this.state.loading}
                component={Link}
                to={`/blog/${topic - 1}`}
              >
                Previous Post
              </Button>
            </Grid>
            <Grid item>
              <Button
                variant="contained"
                endIcon={<ArrowForwardIosIcon />}
                disableElevation
                disabled={
                  !(topic < this.props.posts.length - 1) || this.state.loading
                }
                component={Link}
                to={`/blog/${topic + 1}`}
              >
                Next Post
              </Button>
            </Grid>
          </Grid>
        </Container>
        <MarkdownTopic topic={topicPost} didLoad={this.postDidLoad} />
      </>
    );
  }
}

export default MarkdownTopics;
