import React from "react";
import ReactGA from "react-ga";

import "./Main.css";

import MainPage from "./MainPage";
import AboutComponent from "./about/AboutComponent";
import BlogComponent from "../blog/BlogComponent";
import MarkdownTopics from "../mdTopic/MarkdownTopics";

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";

class Main extends React.Component {
  constructor(props) {
    super(props);

    this.initGA = this.initGA.bind(this);

    this.state = {
      initGA: false,
      Posts: []
    };
  }

  componentDidMount() {
    if (!this.state.initGA) {
      this.initGA();
    }

    fetch("/md/config.json")
      .then((res) => res.json())
      .then((res) => {
        const { Posts } = res;
        this.setState({ Posts });
      });
  }

  initGA() {
    ReactGA.initialize("UA-174166579-1");
    ReactGA.pageview(window.location.pathname + window.location.search);
  }

  render() {
    return (
      <Router>
        <Switch>
          <Route path="/home" exact component={MainPage} />
          <Route path="/about" component={AboutComponent} />
          <Route
            path="/blog"
            exact
            render={(routeProps) => (
              <BlogComponent {...routeProps} {...{ posts: this.state.Posts }} />
            )}
          />
          <Route
            path="/blog/:topic"
            exact
            render={(routeProps) => (
              <MarkdownTopics
                {...routeProps}
                {...{ posts: this.state.Posts }}
              />
            )}
          />
          <Redirect from="*" to="/home" />
        </Switch>
      </Router>
    );
  }
}

export default Main;
