import React from "react";

import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Avatar from "@material-ui/core/Avatar";

import ImageIcon from "@material-ui/icons/Image";
import WorkIcon from "@material-ui/icons/Work";
import BeachAccessIcon from "@material-ui/icons/BeachAccess";
import BlurCircularIcon from "@material-ui/icons/BlurCircular";
import BorderColorIcon from "@material-ui/icons/BorderColor";
import CakeIcon from "@material-ui/icons/Cake";
import Brightness5Icon from "@material-ui/icons/Brightness5";
import BusinessCenterIcon from "@material-ui/icons/BusinessCenter";
import CameraIcon from "@material-ui/icons/Camera";
import CloudIcon from "@material-ui/icons/Cloud";
import ColorLensIcon from "@material-ui/icons/ColorLens";
import DesktopWindowsIcon from "@material-ui/icons/DesktopWindows";
import EmojiEmotionsIcon from "@material-ui/icons/EmojiEmotions";
import EmojiEventsIcon from "@material-ui/icons/EmojiEvents";
import EmojiSymbolsIcon from "@material-ui/icons/EmojiSymbols";
import ExploreIcon from "@material-ui/icons/Explore";
import ExtensionIcon from "@material-ui/icons/Extension";
import FreeBreakfastIcon from "@material-ui/icons/FreeBreakfast";

import Divider from "@material-ui/core/Divider";

import ToolbarComponent from "../../components/ToolbarComponent";

import { Link } from "react-router-dom";

const ICON_LIST = [
  <ImageIcon />,
  <WorkIcon />,
  <BeachAccessIcon />,
  <BlurCircularIcon />,
  <BorderColorIcon />,
  <CakeIcon />,
  <Brightness5Icon />,
  <BusinessCenterIcon />,
  <CameraIcon />,
  <CloudIcon />,
  <ColorLensIcon />,
  <DesktopWindowsIcon />,
  <EmojiEmotionsIcon />,
  <EmojiEventsIcon />,
  <EmojiSymbolsIcon />,
  <ExploreIcon />,
  <ExtensionIcon />,
  <FreeBreakfastIcon />
];

class BlogComponent extends React.Component {
  constructor(props) {
    super(props);

    this.getAnIcon = this.getAnIcon.bind(this);
  }

  componentDidMount() {
    document.getElementsByClassName("appBody")[0].classList.add("aboutPage");
  }

  componentWillUnmount() {
    document
      .getElementsByClassName("aboutPage")[0]
      .classList.remove("aboutPage");
  }

  getAnIcon() {
    return ICON_LIST[Math.floor(Math.random() * 19)];
  }

  render() {
    console.log({ blog: this.props });
    const propPosts = this.props.posts !== undefined ? this.props.posts : [];
    const listMap = propPosts.map((post, i) => {
      return (
        <>
          <ListItem button component={Link} to={`/blog/${i}`} key={i}>
            <ListItemAvatar>
              <Avatar>{this.getAnIcon()}</Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={post.title}
              secondary={`${post.author}, ${post.date}`}
            />
          </ListItem>
          <Divider component="li" />
        </>
      );
    });

    const reverse = listMap.slice().reverse();

    return (
      <>
        <ToolbarComponent />
        <Container maxWidth="md">
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="center"
            spacing={2}
            style={{ marginTop: "10px" }}
          >
            <Grid item style={{ width: "100%" }}>
              <List style={{ width: "100%" }}>{reverse}</List>
            </Grid>
          </Grid>
        </Container>
      </>
    );
  }
}

export default BlogComponent;
